	import service from '../../service.js';
    import {
        mapState,
        mapMutations
    } from 'vuex'
	
    export default {
		data(){
			return {
				userInfo:{},
				release:0,
				focus:0,
				fans:0,
				news:0,
			}
		},
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
        methods: {
            ...mapMutations(['logout','islogin']),
			isLogin(){
				this.islogin();
			},
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
                this.logout();
                /**
                 * 如果需要强制登录跳转回登录页面
                 */
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../login/login',
                    });
                }
            },
			realse(){
				uni.navigateTo({
					url: '../publish/publish',
				});
			},
			chooseImage(){
				var that=this;
				uni.chooseImage({
				    count: 1, //默认9
				    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				    sourceType: ['album','camera'], //从相册选择
				    success: function (res) {
				        console.log(JSON.stringify(res.tempFilePaths));
						that.upload((res.tempFilePaths[0]))
						
				    }
				});
				
			},
			async myinit(){
				var res=await service.request('myinit','GET','',true,service.getCache('token'));
				if(res.code==1){
					this.release=res.data.release;
					this.focus=res.data.focus;
					this.fans=res.data.fans;
					this.news=res.data.news;
				}else{
					uni.showToast({
						icon:'success',
						title:'网络出错'
					})
				}
			},
			async upload(path){
				var res=await service.uploadfile(path,'file');
				let that=this;
				if(res.code==1){
					console.log(222222);
					var avatarpath=res.data.httpurl;
					
					var data={avatar:avatarpath}
					var token=service.getCache('token');
					var avaRes=await service.request('profile','POST',data,true,token);
					if(avaRes.code==1){
						that.userInfo.avatar=avatarpath;
						service.addUser(that.userInfo);
						console.log(avatarpath,that.userInfo);
						uni.showToast({
							icon:'success',
							title:"更换成功"
						});
					}else{
						uni.showToast({
							icon: 'none',
							title: avaRes.msg,
						});
					}
				}else{
					console.log(121212);
					uni.showToast({
						icon: 'none',
						title: res.msg,
					});
				}
			}
			
        },
		onLoad (){
			this.isLogin();
			let userInfo=service.getUsers();
			let that=this;
			that.userInfo=userInfo;
			console.log(userInfo);
			if(that.userInfo){
				this.myinit();
			}
		}
    }
